<template>
  <div class="header">
    <div class="header__body">
      <h1 class="header__logo"><a href="/#/">yami</a></h1>

      <!-- <div class="header__box">
        <div class="header__sigh_in">sigh in</div>
        <div class="header__subscribe">subscribe</div>
      </div> -->
    </div>
    <div class="header__foot">
      <nav class="header__nav">
        <ul>
          <div class="header__date">
            <strong>{{ day }}</strong> <br />
            <span>{{ month }}</span> {{ theDay }}
          </div>
          <li
            v-for="(keyWord, index) in keyWords"
            :key="index"
            :class="
              keyWord.keyWord == $route.params.keyWord
                ? 'header__link-active'
                : 'header__link'
            "
          >
            <router-link
              :to="{
                name: 'news',
                params: { id: keyWord.id, keyWord: keyWord.keyWord },
              }"
              >{{ keyWord.keyWord }}</router-link
            >
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>
<script>
var today = new Date();
var days = [
  "Sunday",
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
];
var months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

var day = days[today.getDay()];
var month = months[today.getMonth()];

var dd = String(today.getDate()).padStart(2, "0");
var yyyy = today.getFullYear();
var theDay = dd + ", " + yyyy;
export default {
  name: "Header",
  computed: {
    keyWords() {
      return this.$store.getters.keyWords;
    },
  },
  data() {
    return {
      show: false,
      day,
      month,
      theDay,
    };
  },
};
</script>

<style scoped>
.header {
  padding: 0 5rem;
}
.header__body {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 2.5px dotted hsl(0, 0%, 80%);
  position: relative;
  height: 81.5px;
}
.header__box {
  display: flex;
  align-items: center;

  text-transform: uppercase;
  font-weight: 700;
  font-size: 1rem;
  font-family: "Georama", sans-serif;
}
.header__box > * {
  cursor: pointer;
}
.header__sigh_in {
  margin-right: 10px;
}
.header__subscribe {
  margin-left: 10px;
  border: 2px solid hsl(0, 0%, 80%);
  padding: 0.75rem 1rem;
}
.header__logo {
  font-size: 2.5rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header__logo a {
  text-decoration: none;
  color: black;
}
.header__foot {
  margin-top: 1rem;
}
.header__date {
  width: fit-content;
  font-family: "Georama", sans-serif;
}
.header__nav {
  display: flex;
  align-items: center;
  width: 100%;
}
.header__nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 100%;
}
.header__link a {
  font-family: "Georama", sans-serif;
  color: unset;
  text-decoration: none;
  text-transform: capitalize;
}
.header__link-active a {
  font-family: "Georama", sans-serif;
  color: unset;
  text-decoration: none;
  text-transform: capitalize;
  font-weight: 700;
}
</style>
