<template>
  <div class="home">
    <Hero :news="news" :isLoading="loadingData" />
    <Subnews :news="news" :startPoint="7" />
  </div>
</template>

<script>
import Subnews from "../components/Sub-news.vue";
import Hero from "../components/Hero.vue";

var today = new Date();
var lastMonth = new Date();

var dd = String(today.getDate()).padStart(2, "0");
var mm = String(today.getMonth() + 1).padStart(2, "0");
var mmLM = String(today.getMonth()).padStart(2, "0");
var yyyy = today.getFullYear();

today = yyyy + "-" + mm + "-" + dd;
lastMonth = yyyy + "-" + mmLM + "-" + dd;

export default {
  name: "Home",
  components: {
    Subnews,
    Hero,
  },
  computed: {
    kWord: function() {
      return this.$store.state.currentKeyWord;
    },
  },
  data() {
    return {
      news: [],
      stocks: [],
      loadingData: true,
    };
  },
  methods: {
    async fetchNews() {
      var basicUrl =
        "https://newsapi.org/v2/everything?" +
        `q=${this.kWord}&` +
        `from=${lastMonth}&` +
        `to=${today}&` +
        "sortBy=publishedAt&" +
        "language=en&" +
        "apiKey=4119d9ff1d7c45e19009a38396300017";

      var request = new Request(basicUrl);
      const res = await fetch(request);

      const data = await res.json();

      return data.articles;
    },
    async fetchStocks() {
      var url =
        "https://api.polygon.io/v2/aggs/ticker/AAPL/range/1/day/2020-06-01/2020-06-17?apiKey=ztbTJnjyrKLdsluXtprPp0iZJ7iNYpAJ";

      var request = new Request(url);
      const res = await fetch(request);

      const data = await res.json();

      return data;
    },
  },
  async created() {
    this.news = await this.fetchNews();
    this.stocks = await this.fetchStocks();
    console.log(this.stocks);
    this.loadingData = false;
  },
};
</script>
