import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const keyWordStore = new Vuex.Store({
  state: {
    currentKeyWord: "world",
    keyWords: [
      { id: 1, keyWord: "world" },
      { id: 2, keyWord: "business" },
      { id: 3, keyWord: "opinion" },
      { id: 4, keyWord: "politics" },
      { id: 5, keyWord: "tech" },
      { id: 6, keyWord: "science" },
      { id: 7, keyWord: "sport" },
      { id: 8, keyWord: "art" },
      { id: 9, keyWord: "book" },
    ],
  },
  mutations: {
    setCurrentKeyWord(state, payload) {
      state.currentKeyWord = payload;
    },
  },
  getters: {
    keyWords: (state) => state.keyWords,
    getKeyWord: (state) => (keyWord) => {
      state.keyWords.find((key) => key.keyWord === keyWord);
    },
  },
  modules: {},
});
